body {
    background-color:navy;
}

.nav {
    display: flex;
    justify-content: center;
}

.nav-img {
    background-color: lightslategrey;
    border-radius: 30px;
    display: flex;
    align-items: center;
    height: 100px;
}


.nav-img h1 {
    color: aliceblue;
    margin: 1rem;
}

.nav-img img {
    height: 100%;
    border-radius: 30px;
    cursor: pointer;
}
.movie-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.movie-item {
    text-align: center;
    width: 15rem;
    height: 25rem;
    margin: 1.2rem;
    color: white;
    background-color: rgb(60, 57, 161);
    border-radius: 1rem;
}
.movie-item img{
    width: 100%;
    height: 90%;
    transition: all 0.8s;
    cursor: pointer;
}
.movie-item img:hover{
    scale: 0.7;
    border-radius: 10rem;
}
.movie-info {
    display: flex;
    justify-content: space-evenly;
    font-size: 1.1rem;
    background-color: lightcoral;
    border-radius: 10px;
}

.movie-info h4 {
    margin: 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
.movie-info span {
    color: yellow;
}

.detail-container{
    display: flex;
    margin: auto;
    width: 80%;
    height: 75vh;
    gap: 10rem;
    margin-top: 3rem;
    border-radius: 1rem;
    background-color: rgb(60, 57, 161);
}
.detail-info{
    width: 50%;
}
.detail-img img{
    width: 400px;
    height: 100%;
    border-radius: 1rem;
}
.detail-title{
    font-size: 1.2rem;
    font-weight: bold;
    color: #fff;
}
.detail-title h3{
    color: coral;
}
.detail-overview{
    color: #c3bdbd;
}
.detail-stats {
    display: flex;
}
.detail-stats h3{
    color: lightskyblue;
}
.detail-stat {
    flex: 1;
    color: antiquewhite;
    font-weight: bold;
}

@media screen and (max-width: 767px) {
    .detail-container{
    display: block;    
    }
.detail-info{
    width: 100%;
    text-align: center;
}
.detail-stats {
display: block;
}
}